.main{
    width: 100%;
    height: 100vh;
}

nav{
    width: 100%;
    height: 5rem;
    /* border: 1px solid black; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 5rem;
    
}
.navleft a{
    text-decoration: none;
    color: black;
}
.navcenter ul{
    display: flex;
}
.navcenter ul li{
    margin: 0 2rem;
    list-style: none;
    cursor: pointer;
}
.navcenter ul li:hover{
    text-shadow: rgba(0,0,0,0.75) 0px 1px 2px;
}
.navright{
    display: none;
}




.state3{
    display: none;
}

@media only screen and (max-width: 400px){
    .navcenter{
        display: none;
    }
    .navright{
        position: absolute;
        top: 10px;
        right: 10px;
        display: block;
    }   
}