*{
    margin: 0;
    box-sizing: border-box;
}
.box{
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    /* align-items: end; */
    
}

.box .btn{
    border: 0;
    outline: 0;
    background-color: transparent;
}


.main i{
    padding: 0.4rem 0.8rem;
    font-size: 20px;
    
}


.content{
    margin-top: 1rem;
    display: flex;
    /* align-items: end; */
    position: absolute;
    right: 10px;
}

.content ul{
    width: 20rem;
    display: flex;
    flex-direction: column;
    background-color: brown;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
}
.content ul li{
    list-style: none;
    margin: 10px 0px;

}
.content ul li:hover{
    text-shadow: yellow 0px 1px 2px;
    color: aquamarine;
}

@media only screen and (max-width: 400px){
    .box{
        position: absolute;
        right: 10px;
    }
    .content{
        top: 3rem;
        position: absolute;
        right: 10px;
    }
    .content ul{
        width: 10rem;
    }   
}